<template>
  <div class="z-view-tabs-pane" :class="classes" v-show="active">
    <slot></slot>
  </div>
</template>
<script>
export default {
  name: "zViewTabsPane",
  inject: ["eventBus"],
  props: {
    name: {
      type: [String, Number],
      required: true
    }
  },
  data() {
    return {
      active: false
    };
  },
  computed: {
    classes() {
      return {
        active: this.active
      };
    }
  },
  mounted() {
    // console.log("tabs-pane  tabs  eventBus");
    // console.log(this.eventBus);
    this.eventBus.$on("update:selected", name => {
      this.active = name === this.name;
      // // console.log("pane  update:selected ", name);
      // if(name === this.name){
      //   console.log(`${name} `)
      //   this.active = true
      // }else{
      //   this.active = false
      //   console.log(`${this.name} `)
      // }
    });
  }
};
</script>
